import { getRandomColor } from "../util";
import React from "react";

const config = {
  stepSize: 20,
  radiusFactor: 0.5,
  numOfPoints: "10",
  strokeWidth: "3px"
};

class Wrong extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    const cir = document.getElementById("circleee");
    function handleMouseMove(event) {
      if (cir) {
        cir.style.transform = `translate(${event.clientX}px,${event.clientY}px)`;
        cir.style.transform += `scale(${(event.clientX % 1.1) +
          0.1 +
          1},${(event.clientY % 1.1) + 0.1})`;
      }
    }
    window.addEventListener("mousemove", handleMouseMove);
  }

  render() {
    const padding = 10;

    return (
      <>
        <div className="svg-container">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1">
            <g
              stroke="black"
              fill="hotpink"
              strokeWidth={config.strokeWidth}
              strokeLinecap="round"
              strokeLinejoin="round"
            >
              <circle r={100} id="circleee" cx={0} cy={0} id="circleee" />
            </g>
          </svg>
        </div>
      </>
    );
  }
}

export default Wrong;
